(function () {
  var enterModule = require('react-hot-loader').enterModule;

  enterModule && enterModule(module);
})();

function fallbackCopyTextToClipboard(text) {
  var textArea = document.createElement("textarea");
  textArea.value = text;
  textArea.style.position = 'fixed';
  textArea.style.opacity = 0;
  document.body.appendChild(textArea);
  textArea.focus();
  textArea.select();

  try {
    var successful = document.execCommand('copy');

    if (!successful) {
      console.log('Fallback: Copying text command was unsuccessful');
    }
  } catch (err) {
    console.error('Fallback: Oops, unable to copy', err);
  }

  document.body.removeChild(textArea);
}

export function copyTextToClipboard(text) {
  if (!navigator.clipboard) {
    fallbackCopyTextToClipboard(text);
    return;
  }

  navigator.clipboard.writeText(text).then(function () {}, function (err) {
    console.error('Async: Could not copy text: ', err);
  });
}
;

(function () {
  var reactHotLoader = require('react-hot-loader').default;

  var leaveModule = require('react-hot-loader').leaveModule;

  if (!reactHotLoader) {
    return;
  }

  reactHotLoader.register(fallbackCopyTextToClipboard, "fallbackCopyTextToClipboard", "/srv/http/cloud/apps/logreader/js/Providers/ClipboardProvider.js");
  reactHotLoader.register(copyTextToClipboard, "copyTextToClipboard", "/srv/http/cloud/apps/logreader/js/Providers/ClipboardProvider.js");
  leaveModule(module);
})();

